@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-weight:400}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*,h1,h3,h2,h4{margin:0;padding:0;box-sizing:border-box;outline:none}body{background:#f2f9f5;line-height:1.7}img{max-width:100%}.App{text-align:center}.App .App-header{background-color:white;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.Header{max-width:100%;background:#444;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;padding:10px 30px;box-sizing:border-box}.Header ul.logo{list-style-type:none;margin:0;padding:0}.Header ul.logo li a{display:flex;align-items:center}.Header ul.logo li a img{max-width:45%}.Header ul.links{display:flex;list-style-type:none;margin:0;padding:0}.Header ul.links li{margin-left:1px;padding:0}.Header ul.links li a{text-decoration:none;color:#f2f5f9;background-color:blue;padding:0.5rem 1.2rem}@media screen and (max-width: 600px){ul.links{display:none}ul.links li{margin-top:1rem}ul.links a{font-size:0.8rem;margin-top:3rem}}.Digital{box-sizing:border-box}.Digital section{display:grid;grid-gap:20px;box-shadow:3px 0px 10px 1px inset rgba(0,0,0,0.1)}.Digital section:not(:first-of-type){padding-top:3px;padding-bottom:5rem}.Digital section img{max-width:80%;margin-left:auto;margin-right:auto;margin-top:10px;margin-bottom:20px}.Digital section p{line-height:2;font-size:0.9rem;text-align:left;padding:3rem 0.97rem 5rem 0.97rem}@media screen and (min-width: 500px){.Digital section img{max-width:70%}}@media screen and (min-width: 768px){.Digital section:nth-of-type(odd){grid-template-columns:1fr 1fr;grid-template-areas:'. title' 'image content';padding-top:3rem;padding-bottom:5rem}.Digital section:nth-of-type(even){grid-template-columns:1fr 1fr;grid-template-areas:'title .' 'content image';padding-top:3rem;padding-bottom:5rem}.Digital section h3{grid-area:title}.Digital section img{grid-area:image;max-width:100%;margin:auto}.Digital section p{font-size:1rem;grid-area:content;padding:0.97rem;display:flex;align-items:center}}@media screen and (min-width: 1024px){.Digital section h3{font-size:1.5rem;letter-spacing:0.1rem}.Digital section img{max-width:80%}.Digital section p{font-size:1.2rem;padding:0 1.4rem}.Digital section:nth-of-type(5) img{max-width:70%}}.Identity{box-sizing:border-box}.Identity section{display:grid;grid-gap:20px;box-shadow:3px 0px 10px 1px inset rgba(0,0,0,0.1)}.Identity section:not(:first-of-type){padding-top:3px;padding-bottom:5rem}.Identity section img{max-width:80%;margin-left:auto;margin-right:auto;margin-top:10px;margin-bottom:20px}.Identity section p{line-height:2;font-size:0.9rem;text-align:left;padding:3rem 0.97rem 5rem 0.97rem}@media screen and (min-width: 500px){.Identity section img{max-width:70%}}@media screen and (min-width: 768px){.Identity section:nth-of-type(odd){grid-template-columns:1fr 1fr;grid-template-areas:'image title' 'image content';padding-top:3rem;padding-bottom:5rem}.Identity section:nth-of-type(even){grid-template-columns:1fr 1fr;grid-template-areas:'title image' 'content image';padding-top:3rem;padding-bottom:5rem}.Identity section h3{grid-area:title}.Identity section img{grid-area:image;max-width:100%;margin:auto}.Identity section p{font-size:1rem;grid-area:content;padding:0.97rem;display:flex;flex-direction:column;align-items:center}}@media screen and (min-width: 1024px){.Identity section h3{font-size:1.5rem;letter-spacing:0.1rem}.Identity section img{max-width:80%}.Identity section p{font-size:1.2rem;padding:0 1.4rem}}.Identity .Personal-Branding img{max-width:50%}

